@import url(https://fonts.googleapis.com/css2?family=Poppins:wghts@400,500,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-size: 16px;
    font-family: "Poppins";
    background-color: hsl(0, 100%, 74%);
    background-image: url(/react-app/static/media/bg-intro-mobile.02ce3982.png);
    background-repeat: no-repeat;
    background-size: cover; 
}

.container {
    padding: 20px;
    color :white;
}

.info > input {   
    width: 100%;
    border-radius: 8px;
    margin: 10px;
    border: 1px solid hsl(249,10% ,26%);
    padding: 15px;
    font-weight: 600;
}  

.left {
    text-align: center;
    padding: 10px;  
}
.left > h1{
    font-size: 32px;
    margin-bottom: 10px;
}
.left > p {
    font-size: 24px;
}
.right {
    padding: 10px;  
}
.trial {
    margin: 10px auto;
    padding: 15px 10px;
    background-color: blue;
    text-align: center;
    border-radius: 10px;
}
.trial > a,
.trial > a:hover{
    color: white;
    text-decoration: none;
}
.form-container {
    background-color: white;
    border-radius: 10px;
    padding: 40px;
    color: black;
}
.form-btn {
    background-color: green;
    padding: 10px;
    width: 100%;
    border-radius: 10px;
    color: white;
    margin: 10px auto;
    text-transform: uppercase;
    -webkit-align-content: center;
            align-content: center;
}
.terms {
    text-align: center;
}
.terms > a,
.terms > a:hover{
    color: hsl(0, 100%, 74%);
    text-decoration: none;
}   
/** ipad view */
 @media only screen and (min-width: 600px) { 
    .container {
        display:-webkit-flex;
        display:flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
        padding: 40px;   
    }
    .container > div {
        -webkit-flex: 1 1;
                flex: 1 1;
        margin: 10px;
    }
    .left {
        text-align: left;
        padding: 20px;
    }    
} 

/* desktop view */
@media only screen and (min-width: 768px) {
    body {
        background-image: url(/react-app/static/media/bg-intro-desktop.059183ee.png)
    }
    .container {
        padding: 100px;   
    }
    .left > h1{
        font-size: 52px;
    }
    .left > p {
        font-size: 32px;  
    }
} 
